<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <!--工具条-->
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="所属监测对象、扬尘监测点简称、设备编号过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="sites"
                v-loading="listLoading"
                :row-class-name="$tableRowClassName"
                style="width: 100%"
            >
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="groupshortname" label="所属分组">
                </el-table-column>
                <el-table-column prop="parentshortname" label="所属监测对象">
                </el-table-column>
                <el-table-column
                    prop="siteshortname"
                    label="扬尘监测点简称"
                ></el-table-column>
                <el-table-column prop="devicecode" label="设备编号">
                    <template slot-scope="scope">
                        <a
                            @click="cancelDialog(scope.row)"
                            style="color: #409eff; cursor: pointer"
                            v-if="canBind"
                            >{{
                                scope.row.devicecode
                                    ? scope.row.devicecode
                                    : "绑定设备"
                            }}</a
                        >
                        <span v-else>{{ scope.row.devicecode }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                width="60%"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
            >
                <el-row>
                    <el-col :span="16">
                        <el-form
                            :model="editForm"
                            label-width="120px"
                            :rules="editFormRules"
                            ref="editForm"
                        >
                            <el-row>
                                <el-col :span="12">
                                    <el-form-item
                                        label="所属分组"
                                        prop="GROUPID"
                                    >
                                        <el-select
                                            v-model="editForm.GROUPID"
                                            filterable
                                            placeholder="请选择"
                                            @change="groupChange"
                                            disabled
                                        >
                                            <el-option
                                                v-for="item in groups"
                                                :key="item.GROUPID"
                                                :label="item.groupshortname"
                                                :value="item.GROUPID"
                                            >
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item
                                        label="所属监测对象"
                                        prop="PARENTSITEID"
                                    >
                                        <el-select
                                            v-model="editForm.PARENTSITEID"
                                            filterable
                                            placeholder="请选择"
                                            @change="siteChange"
                                            disabled
                                        >
                                            <el-option
                                                v-for="item in selectSites"
                                                :key="item.SITEID"
                                                :label="item.siteshortname"
                                                :value="item.SITEID"
                                            >
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item
                                        label="简称"
                                        prop="siteshortname"
                                    >
                                        <el-input
                                            v-model="editForm.siteshortname"
                                            auto-complete="off"
                                            maxlength="8"
                                        ></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-row>
                                        <el-col :span="16">
                                            <el-form-item
                                                label="经纬度"
                                                prop="sitelng"
                                                ><el-input
                                                    v-model="editForm.sitelng"
                                                    auto-complete="off"
                                                    maxlength="30"
                                                ></el-input></el-form-item
                                        ></el-col>
                                        <el-col :span="8">
                                            <el-form-item
                                                prop="sitelat"
                                                label-width="0"
                                            >
                                                <el-input
                                                    v-model="editForm.sitelat"
                                                    auto-complete="off"
                                                    maxlength="30"
                                                ></el-input>
                                            </el-form-item>
                                        </el-col>
                                    </el-row>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="开始日期"
                                        prop="startdate"
                                    >
                                        <el-date-picker
                                            v-model="editForm.startdate"
                                            type="date"
                                            placeholder="选择开始日期"
                                        >
                                        </el-date-picker
                                    ></el-form-item>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="结束日期"
                                        prop="enddate"
                                    >
                                        <el-date-picker
                                            v-model="editForm.enddate"
                                            type="date"
                                            placeholder="选择结束日期"
                                        >
                                        </el-date-picker
                                    ></el-form-item>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="推送到市里"
                                        prop="bpush"
                                    >
                                        <el-select
                                            v-model="editForm.bpush"
                                            filterable
                                            placeholder="请选择"
                                        >
                                            <el-option
                                                :key="1"
                                                label="是"
                                                :value="1"
                                            >
                                            </el-option>
                                            <el-option
                                                :key="0"
                                                label="否"
                                                :value="0"
                                            >
                                            </el-option> </el-select></el-form-item
                                ></el-col>
                            </el-row>
                        </el-form>
                    </el-col>
                    <el-col :span="8">
                        <mark-map
                            ref="myMap"
                            v-if="editFormVisible"
                            :markposition="{
                                lng: editForm.sitelng,
                                lat: editForm.sitelat,
                            }"
                            @callMap="callEditMap"
                        ></mark-map>
                    </el-col>
                </el-row>

                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                width="60%"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
            >
                <el-row>
                    <el-col :span="16">
                        <el-form
                            :model="addForm"
                            label-width="120px"
                            :rules="addFormRules"
                            ref="addForm"
                        >
                            <el-row>
                                <el-col :span="12">
                                    <el-form-item
                                        label="所属分组"
                                        prop="GROUPID"
                                    >
                                        <el-select
                                            v-model="addForm.GROUPID"
                                            filterable
                                            placeholder="请选择"
                                            @change="groupChange"
                                        >
                                            <el-option
                                                v-for="item in groups"
                                                :key="item.GROUPID"
                                                :label="item.groupshortname"
                                                :value="item.GROUPID"
                                            >
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item
                                        label="所属监测对象"
                                        prop="PARENTSITEID"
                                    >
                                        <el-select
                                            v-model="addForm.PARENTSITEID"
                                            filterable
                                            placeholder="请选择"
                                            @change="siteChange"
                                        >
                                            <el-option
                                                v-for="item in selectSites"
                                                :key="item.SITEID"
                                                :label="item.siteshortname"
                                                :value="item.SITEID"
                                            >
                                            </el-option>
                                        </el-select>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-form-item
                                        label="简称"
                                        prop="siteshortname"
                                    >
                                        <el-input
                                            v-model="addForm.siteshortname"
                                            auto-complete="off"
                                            maxlength="20"
                                        ></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="12">
                                    <el-row>
                                        <el-col :span="16">
                                            <el-form-item
                                                label="经纬度"
                                                prop="sitelng"
                                                ><el-input
                                                    v-model="addForm.sitelng"
                                                    auto-complete="off"
                                                    maxlength="30"
                                                ></el-input></el-form-item
                                        ></el-col>
                                        <el-col :span="8">
                                            <el-form-item
                                                prop="sitelat"
                                                label-width="0"
                                            >
                                                <el-input
                                                    v-model="addForm.sitelat"
                                                    auto-complete="off"
                                                    maxlength="30"
                                                ></el-input>
                                            </el-form-item>
                                        </el-col>
                                    </el-row>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="开始日期"
                                        prop="startdate"
                                    >
                                        <el-date-picker
                                            v-model="addForm.startdate"
                                            type="date"
                                            placeholder="选择开始日期"
                                        >
                                        </el-date-picker
                                    ></el-form-item>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="结束日期"
                                        prop="enddate"
                                    >
                                        <el-date-picker
                                            v-model="addForm.enddate"
                                            type="date"
                                            placeholder="选择结束日期"
                                        >
                                        </el-date-picker
                                    ></el-form-item>
                                </el-col>
                                <el-col :span="12"
                                    ><el-form-item
                                        label="推送到市里"
                                        prop="bpush"
                                    >
                                        <el-select
                                            v-model="addForm.bpush"
                                            filterable
                                            placeholder="请选择"
                                        >
                                            <el-option
                                                :key="1"
                                                label="是"
                                                :value="1"
                                            >
                                            </el-option>
                                            <el-option
                                                :key="0"
                                                label="否"
                                                :value="0"
                                            >
                                            </el-option> </el-select></el-form-item
                                ></el-col>
                            </el-row>
                        </el-form>
                    </el-col>
                    <el-col :span="8">
                        <mark-map
                            ref="myMap"
                            v-if="addFormVisible"
                            :markposition="{
                                lng: addForm.sitelng,
                                lat: addForm.sitelat,
                            }"
                            @callMap="callAddMap"
                        ></mark-map>
                    </el-col>
                </el-row>

                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <el-dialog
                title="扬尘监测点绑定设备"
                width="50%"
                :visible.sync="bindFormVisible"
                v-model="bindFormVisible"
                :close-on-click-modal="false"
                :show-close="false"
            >
                <el-row>
                    <el-form
                        ref="bindForm"
                        :model="bindForm"
                        label-width="100px"
                        :rules="bindFormRules"
                    >
                        <el-row>
                            <el-col :span="12">
                                <el-form-item label="扬尘监测点">
                                    <el-input
                                        disabled
                                        v-model="bindForm.parentshortname"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="12">
                                <el-form-item
                                    label="绑定设备"
                                    prop="devicecode"
                                >
                                    <el-select
                                        v-model="bindForm.devicecode"
                                        placeholder="请选择"
                                    >
                                        <el-option
                                            v-for="item in unbinds"
                                            :key="item.devicecode"
                                            :label="item.devicecode"
                                            :value="item.devicecode"
                                        >
                                        </el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>
                    </el-form>
                </el-row>
                <div slot="footer" class="dialog-footer">
                    <el-button
                        @click.native="
                            bindFormVisible = false;
                            bindForm.devicecode = '';
                        "
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="bindSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getDustPageList,
    getDustGroups,
    City,
    removeDust,
    editDust,
    addDust,
    getDust,
    getUnBind,
    bindDevice,
    unbindDevice,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import MarkMap from "../../components/MarkMap";
import dicmixin from "../../assets/dic/dicmixin";
export default {
    components: { leftgroup, Toolbar, MarkMap },
    mixins: [dicmixin],
    data() {
        return {
            buttonList: [],
            filters: {
                name: "",
            },
            sites: [],
            groupCounts: [],
            groups: [],
            unbinds: [],
            selectSites: [],
            listLoading: false,
            page: 1,
            pageSize: 10,
            total: 0,
            ordertype: "",
            order: "",
            selGroupId: 0,
            sitetypes: [
                { text: "建筑工地", value: 172 },
                { text: "市政工程", value: 188 },
            ],
            citys: [],
            districts: [],
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择所属分组",
                        trigger: "blur",
                    },
                ],
                PARENTSITEID: [
                    {
                        required: true,
                        message: "请选择所属监测对象",
                        trigger: "blur",
                    },
                ],
                siteshortname: [
                    {
                        required: true,
                        message: "请输入简称",
                        trigger: "blur",
                    },
                ],
                startdate: [
                    {
                        required: true,
                        message: "请输入开始日期",
                        trigger: "blur",
                    },
                ],
                bpush: [
                    {
                        required: true,
                        message: "请选择是否推送到市里",
                        trigger: "blur",
                    },
                ],
                sitelng: [
                    {
                        required: true,
                        message: "请输入经度",
                        trigger: "blur",
                    },
                ],
                sitelat: [
                    {
                        required: true,
                        message: "请输入纬度",
                        trigger: "blur",
                    },
                ],
            },
            //编辑界面数据
            editForm: {
                GroupId: "",
            },
            bindForm: {},
            bindFormVisible: false,
            bindFormRules: {
                devicecode: [
                    {
                        required: true,
                        message: "请选择扬尘设备",
                        trigger: "blur",
                    },
                ],
            },
            addFormVisible: false, //新增界面是否显示
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择所属分组",
                        trigger: "blur",
                    },
                ],
                PARENTSITEID: [
                    {
                        required: true,
                        message: "请选择所属监测对象",
                        trigger: "blur",
                    },
                ],
                siteshortname: [
                    {
                        required: true,
                        message: "请输入简称",
                        trigger: "blur",
                    },
                ],
                startdate: [
                    {
                        required: true,
                        message: "请输入开始日期",
                        trigger: "blur",
                    },
                ],
                bpush: [
                    {
                        required: true,
                        message: "请选择是否推送到市里",
                        trigger: "blur",
                    },
                ],
                sitelng: [
                    {
                        required: true,
                        message: "请输入经度",
                        trigger: "blur",
                    },
                ],
                sitelat: [
                    {
                        required: true,
                        message: "请输入纬度",
                        trigger: "blur",
                    },
                ],
            },
            addForm: {
                GroupId: "",
            },
            canEdit: false,
            canDelete: false,
            canBind: false,
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.getDustPage(val);
        },
        getDustPage(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            this.listLoading = true;
            let paras = {
                groupid: this.selGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
                order: this.order,
                ordertype: this.ordertype,
            };
            getDustPageList(paras).then((res) => {
                if (res.success) {
                    this.sites = res.data.data;
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        getGroupCount() {
            getDustGroups().then((res) => {
                this.groupCounts = res.data;
            });
        },
        callbackGroup(item) {
            this.selGroupId = item.GROUPID;
            this.page = 1;
            this.getDustPage();
        },
        siteChange(val) {
            let sites = this.selectSites.filter((res) => {
                return res.SITEID == val;
            });
            if (sites && sites.length > 0) {
                this.$refs.myMap.changeLoc(
                    sites[0].parentlng,
                    sites[0].parentlat
                );
                if (this.addFormVisible) {
                    this.$set(this.addForm, "sitelng", sites[0].sitelng);
                    this.$set(this.addForm, "sitelat", sites[0].sitelat);
                } else if (this.editFormVisible) {
                    this.$set(this.editForm, "sitelng", sites[0].parentlng);
                    this.$set(this.editForm, "sitelat", sites[0].parentlat);
                }
            }
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }

            if (row.devicecode) {
                this.$message({
                    message: "请先解绑设备！",
                    type: "error",
                });
                return;
            }

            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { siteid: row.SITEID };
                    removeDust(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getDustPage(this.page);
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }
            this.groupChange(row.GROUPID);
            let para = { id: row.SITEID };
            getDust(para).then((res) => {
                if (res.success) {
                    this.editForm = Object.assign({}, res.data);
                    this.editFormVisible = true;
                } else {
                    this.editFormVisible = false;
                    this.$message({
                        message: "获取信息错误",
                        type: "error",
                    });
                }
            });
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = {};
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    editDust(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getDustPage(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let para = Object.assign({}, this.addForm);

                    addDust(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getDustPage(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "PARENTSITEID", "");
            this.$set(this.editForm, "PARENTSITEID", "");
            this.selectSites = this.groupSites;
        },
        cancelDialog(row) {
            if (!row.devicecode) {
                this.bindForm = Object.assign({}, row);
                this.bindFormVisible = true;

                getUnBind({ groupid: row.GROUPID }).then((res) => {
                    this.unbinds = res.data;
                });
            } else {
                this.$confirm("确定要解绑吗？", "提示", {}).then(() => {
                    let para = {
                        groupid: row.GROUPID,
                        siteid: row.SITEID,
                        deviceCode: row.devicecode,
                    };

                    unbindDevice(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "解绑成功",
                                type: "success",
                            });
                            this.getDustPage(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                });
            }
        },
        bindSubmit() {
            let _this = this;
            this.$refs.bindForm.validate((valid) => {
                if (valid) {
                    let para = {
                        groupid: this.bindForm.GROUPID,
                        siteid: this.bindForm.SITEID,
                        deviceCode: this.bindForm.devicecode,
                    };

                    bindDevice(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "绑定成功",
                                type: "success",
                            });
                            this.$refs["bindForm"].resetFields();
                            this.bindFormVisible = false;
                            this.getDustPage(this.page);
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                    });
                    //});
                }
            });
        },
        callAddMap(item) {
            this.$set(this.addForm, "sitelng", item.point.lng);
            this.$set(this.addForm, "sitelat", item.point.lat);
        },
        callEditMap(item) {
            this.editForm.sitelng = item.point.lng;
            this.editForm.sitelat = item.point.lat;
        },
        // sortChange(column) {
        //     if (column.order == "ascending") {
        //         this.ordertype = "asc";
        //     } else if (column.order == "descending") {
        //         this.ordertype = "desc";
        //     }
        //     this.order = column.prop;
        //     this.getDustPage();
        // },
    },
    mounted() {
        this.getDustPage();
        this.getGroups();
        this.getGroupCount();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        if (buttons) {
            buttons.forEach((element) => {
                //&& element.IsButton && !element.IsHide
                if (element.Func == "handleEdit") {
                    this.canEdit = true;
                } else if (element.Func == "handleDel") {
                    this.canDelete = true;
                } else if (element.Func == "cancelDialog") {
                    this.canBind = true;
                } else {
                    this.buttonList.push(element);
                }
            });
        }
    },
};
</script>
<style scoped>
.el-select {
    width: 100%;
}
.box-card .text {
    font-size: 14px;
}
.box-card .item {
    cursor: pointer;
    padding: 10px;
}

.box-card .item.active {
    background: #ebf5ff;
}

.box-card .item:hover {
    background: #ebf5ff;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
    width: 100%;
}

.bm-view {
    padding-left: 10px;
    width: 100%;
    height: 232px;
}
</style>


